import React from 'react';
import { Fade } from 'react-awesome-reveal';
import styled from 'styled-components';
import { Burger } from './Burger';
import { theme } from './../../../globalStyles';
import { Link } from 'react-scroll';

export const Navbar = () => {
    return (
        <Nav>
            <div>
                <Fade direction='bottom' duration='2000' triggerOnce={true}>
                    <Link
                        activeClass='active'
                        to='home'
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={1000}
                    >
                        <div className='logo'>dsaez</div>
                    </Link>
                </Fade>
                <Burger />
            </div>
        </Nav>
    );
};

const Nav = styled.nav`
    display: flex;
    height: 4rem;
    z-index: 999;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    position: fixed;
    width: 100%;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #212336;
        z-index: -1;
        opacity: 0.8;
    }

    > div {
        width: ${theme.bpExtraMedium};
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 0 2rem;
    }

    .logo {
        color: #fff;
        font-size: 1.5rem;
        font-weight: bold;
        padding: 15px 0;
        text-transform: uppercase;
        z-index: 20;
        cursor: pointer;

        &::first-letter {
            color: ${theme.primaryColor};
        }
    }
`;
