import React from 'react';
import Particles from 'react-tsparticles';
import styled from 'styled-components';

import { particles } from './ui/particles';
import './ui/particles.css';
import { Draw } from './ui/Draw';
import { theme } from './../globalStyles';
import { Link } from 'react-scroll';

export const Home = () => {
    return (
        <>
            <div id='home' />
            <Particles
                id='tsparticles'
                options={particles}
                style={{ padding: '0' }}
            />
            <Draw style={{ zIndex: '1' }} />
            <HomeContainer style={{ zIndex: '2' }}>
                <HomeTitle>
                    Hello, I'm a <br />
                    Front-end Developer
                </HomeTitle>
                <HomeSubTitle>
                    Bienvenido a mi portafolio personal donde podras encontrar
                    una variedad de trabajos realizados en base a React.js
                </HomeSubTitle>
                <Link
                    activeClass='active'
                    to='projects'
                    spy={true}
                    smooth={true}
                    offset={-70}
                    duration={2000}
                >
                    <button>Ver proyectos</button>
                </Link>
            </HomeContainer>
        </>
    );
};

const HomeContainer = styled.div`
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    width: 100%;
    @media (max-width: ${theme.bpSmall}) {
        justify-content: flex-end;
    }
`;

const HomeTitle = styled.h1`
    color: #fff;
    font-size: 2.5rem;
`;

const HomeSubTitle = styled.p`
    padding: 0.5rem 0;
    width: 500px;
    font-size: 1rem;

    @media (max-width: ${theme.bpExtraSmall}) {
        width: 100%;
    }
`;
