import { createGlobalStyle } from 'styled-components';

export const theme = {
    bpExtraSmall: '36rem', //576px
    bpSmall: '48rem', // 768px
    bpExtraMedium: '62rem', //992px
    bpMedium: '64rem', // 1024px
    bpLarge: '85.375rem', // 1366px
    bpXlarge: '120rem', // 1920px
    bpXxlarge: '160rem', // 2560px

    primaryColor: '#f44653',
    primaryColorDark: '#ff4a57',
    secondaryColor: '#23263A',
    tertiaryColor: '#212437',
    whiteColor: '#fff',

    shadow: '0px 14px 32px 0px rgba(0, 0, 0, 0.15)'
};

export const GlobalStyles = createGlobalStyle`
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
}

body { 
    background: ${theme.secondaryColor}; 
}

p {
    color: ${theme.whiteColor};
    padding: .2rem 0;
    font-weight: 300;
    font-size: calc(.5vw + .5rem);
}

span {
    color: ${theme.whiteColor};
    padding: .2rem 0;
    font-weight: 300;
    font-size: 0.8rem; 
}

button, input, textarea {
    border: none;
    outline: none;
    border-radius: 3px;
}

input:focus, textarea:focus {
    color: #ff4a57;
}



button{
  color: ${theme.whiteColor};
  background: #ff4a57;
  border: none;
  cursor: pointer;
  height: 3rem;
  margin: 1rem 0px;
  width: 10rem;
  font-size: 0.9rem;
  font-weight: 600;

  &:hover {
    background: ${theme.primaryColorDark};
    transition: 0.3s;
  }
}


.title__section{
  color: ${theme.primaryColor};
  width: fit-content;
  padding: 2rem 0;
  font-size: 2rem;

  &::after {
    content: "";
    display: block;
    background-color: ${theme.primaryColor};
    height: 3px;
    width: 60%;
  }
}
`;
