import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { Modal } from './Modal';
import styled from 'styled-components';
import { portfolioImages } from './../../helpers/helperImages';
import { Rotate } from 'react-awesome-reveal';
import axios from 'axios';
import { theme } from './../../globalStyles';

const schema = yup.object().shape({
    name: yup.string().required('Nombre es requerido'),
    email: yup
        .string()
        .email('Por favor ingrese un email v√°lido')
        .required('Email es requerido'),
    message: yup
        .string()
        .required('Mensaje es requerido')
        .min(10, 'M√≠nimo 10 caracteres')
        .max(100, 'M√°ximo 100 caracteres')
        .matches(/^[a-zA-Z0-9 ]*$/, 'S√≥lo se permiten letras y n√∫meros'),
});

export const ContactForm = () => {
    const [estadoModal, setEstadoModal] = useState(false);

    const {
        register,
        handleSubmit,
        formState: { errors },
        reset,
    } = useForm({
        resolver: yupResolver(schema),
    });

    const onSubmit = async (data) => {
        axios
            .post('https://portfolio-dsaez.herokuapp.com/api/', data)
            .then((res) => {
                setEstadoModal(true);
                reset();
                console.log(res);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    return (
        <>
            <form onSubmit={handleSubmit(onSubmit)} id='form-validation'>
                <label htmlFor='name'>Nombre</label>
                <input
                    id='name'
                    type='text'
                    placeholder='Nombre y apellido'
                    {...register('name')}
                />
                <Error>
                    <p>{errors.name?.message}</p>
                </Error>
                <label htmlFor='email'>Email</label>
                <input
                    id='email'
                    type='text'
                    placeholder='Email'
                    {...register('email', {
                        required: true,
                    })}
                />
                <Error>
                    <p>{errors.email?.message}</p>
                </Error>
                <label htmlFor='message'>Mensaje</label>
                <textarea
                    id='message'
                    placeholder='Mensaje'
                    rows='10'
                    cols='50'
                    {...register('message')}
                />
                <Error>
                    <p>{errors.message?.message}</p>
                </Error>
                <button type='submit'>Enviar</button>
            </form>

            <Modal
                estado={estadoModal}
                cambiarEstado={setEstadoModal}
                mostrarHeader={true}
                mostrarOverlay={true}
                posicionModal={'center'}
                padding={'4rem 2rem 1rem'}
            >
                <Contenido>
                    <Rotate>
                        <img
                            src={portfolioImages('./check.png').default}
                            alt='check'
                        />
                    </Rotate>
                    <Titulo>√âxito</Titulo>
                    <p>Su mensaje ha sido enviado correctamente</p>
                    <Boton onClick={() => setEstadoModal(false)}>Aceptar</Boton>
                </Contenido>
            </Modal>
        </>
    );
};

const Boton = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem 0;
    border-radius: 100px;
    width: 200px;
    height: 40px;
    border: none;
    background: ${theme.primaryColor};
    cursor: pointer;
    font-weight: 500;
    color: #fff;
    &:hover {
        background: ${theme.primaryColorDark};
        transition: 0.3s;
    }
`;

const Contenido = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const Titulo = styled.h1`
    color: #fff;
    font-size: 2rem;
    font-weight: 500;
    margin-top: 1rem;
`;

const Error = styled.div`
    width: 100%;
    display: flex;
    justify-content: flex-start;

    p {
        font-weight: 500;
        font-size: calc(0.7rem + 0.2vw);
        padding: 1rem 0;
    }
`;
