import React from 'react';
import styled from 'styled-components';
import { portfolioImages } from './../helpers/helperImages';
import { theme } from './../globalStyles';
import { portfolioDownload } from './../helpers/helperDownload';

export const About = () => {
    return (
        <>
            <div id='about' />
            <h2 className='title__section'>Sobre m√≠</h2>

            <AboutCard>
                <ProfileBackgroundImage
                    src={portfolioImages('./astronout.png').default}
                    alt='Astronout'
                />
                <ProfileImage
                    src={portfolioImages('./profile.png').default}
                    alt='Profile Image'
                />
                <Profile>
                    <h1>Daniel S√°ez</h1>
                    <span>Analista programador</span>
                    <div>
                        <a
                            href='https://www.linkedin.com/in/daniel-s%C3%A1ez-hern%C3%A1ndez-361081115/'
                            target='_blank'
                            rel='noreferrer'
                        >
                            <img
                                src={portfolioImages('./linkedin.png').default}
                                alt='Linkedin'
                                title='Linkedin'
                            />
                        </a>
                        <a
                            href='https://github.com/dsaez27/'
                            target='_blank'
                            rel='noreferrer'
                        >
                            <img
                                src={portfolioImages('./github.png').default}
                                alt='Github'
                                title='Github'
                            />
                        </a>
                        <a
                            href='mailto:dssh27@gmail.com?subject=Contacto desde el Portafolio'
                            target='_blank'
                            rel='noreferrer'
                        >
                            <img
                                src={portfolioImages('./email.png').default}
                                alt='Email'
                                title='Email'
                            />
                        </a>
                    </div>
                    <ProfileText>
                        "Soy amante de la programaci√≥n, me encantan los
                        desafios, me gusta aprender y trabajar en equipo.
                        Actualmente me encuentro en la realizacion dede algunos
                        cursos para aumentar mis habilidades como desarrollador
                        web."
                    </ProfileText>
                    <DownloadText
                        href={portfolioDownload('./Daniel_Saez_CV.pdf').default}
                        target='_blank'
                        rel='noreferrer'
                        title='Descargar CV'
                    >
                        <img
                            src={portfolioImages('./curriculum.png').default}
                            alt='Curriculum Vitae'
                        />
                        Descargar CV
                    </DownloadText>
                </Profile>
            </AboutCard>
        </>
    );
};

const AboutCard = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #2b2d41;
    border-radius: 0.5rem;
    box-shadow: ${theme.shadow};
`;

const ProfileBackgroundImage = styled.img`
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 0.5rem 0.5rem 0 0;
`;

const ProfileImage = styled.img`
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 50%;
    transform: translateY(75%);
    position: absolute;

    @media (max-width: 768px) {
        width: 100px;
        height: 100px;
        transform: translateY(150%);
    }
`;

const Profile = styled.div`
    width: 100%;
    height: 100%;
    min-height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4rem 0;

    h1 {
        font-size: 2rem;
        color: ${theme.primaryColor};
        padding: 0.5rem;
    }

    div {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.5rem;
        padding: 0.5rem;
    }

    span {
        font-weight: 300;
        font-style: italic;
    }

    img {
        width: 32px;
        height: 32px;
        cursor: pointer;
    }
`;

const ProfileText = styled.p`
    width: min(300px, 100%);
    height: 100%;
    text-align: center;
    padding: 1rem;
    font-size: 1rem;
`;

const DownloadText = styled.a`
    color: ${theme.primaryColor};
    font-size: 1rem;
    text-decoration: none;
    cursor: pointer;
    transition: 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0.5rem;
    padding: 1rem;

    &:hover {
        text-decoration: underline;
    }
`;
